:root {
  /* 그라디언트 색상1 */
  --main-bg-color-deepdark: rgb(0, 15, 26);
  /* 안쓰이고 있음 */
  --main-bg-color-dark: rgba(1, 36, 60, 1);
  /* 그라디언트 색상2 */
  --main-bg-color: rgba(1, 41, 69, 1);
  /* 안쓰이고 있음 */
  --main-bg-color-light: rgba(1, 41, 69, 0.5);

  /* 주요 텍스트, 차트 선, 버튼 색상 */
  --main-text-color: rgba(96, 255, 239, 1);
  /* 안쓰이고 있음 */
  --main-text-color-dark: rgba(96, 255, 239, 0.8);
  /* 타이틀 색상으로 쓰이고 있음 */
  --sub-text-color: rgb(255 208 106);
  --sub-text-color-light: rgb(222, 255, 252);

  /* 차트 각각의 배경색으로 쓸까 했는데 없는게 더 이쁨 */
  --chart-bg-color: rgb(1, 32, 49);

  /* 유저 컨테이너 색상 */
  --compo-bg-color: rgb(22 85 97);

  --risk-bg-color: "rgba(254, 180, 123, 0.2)";
  --risk-fg-color: "tomato";
}
