@import url("https://fonts.googleapis.com/css2?family=Sunflower:wght@300&display=swap");
@import url("setTheme.css");

:root {
}

body {
  /* background-color: var(--main-bg-color-deepdark); */
  background: radial-gradient(
    circle,
    var(--main-bg-color),
    var(--main-bg-color-deepdark)
  );
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  /* color: var(--sub-text-color); */

  font-family: "Sunflower", sans-serif;
  font-weight: 500;
  font-style: normal;
  font-weight: bold;
}

.App {
  /* width: 100vw; */
  height: 100vh;

  text-align: center;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.section {
  display: flex;
  flex-direction: row;
}
.section0 {
  width: 100%;
}
.section1 {
  flex-direction: column;
  width: 350px;
  max-width: 350px;
}

.section2 {
  flex-direction: column;
  width: 420px;
  max-width: 1220px;
  align-items: start;
  /* flex-wrap: wrap; */
}

.section3 {
  width: 80px;
  max-width: 80px;
  display: flex;
  align-content: flex-start;
  align-items: flex-start;
  justify-content: flex-start;
}
.empty {
  width: 30px !important;
  max-width: 30px;
  min-width: 30px;
}
.verticalEmpty {
  height: 20px;
  max-height: 20px;
  min-height: 15px;
}
.verticalEmpty2 {
  width: 100%;
  height: 85px;
}
.main {
  color: white;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.chartBox {
  cursor: pointer;
  padding: 10px;
  width: 100%;
  height: 100%;
}
.chartBox:hover {
  border-radius: 15px;
  box-shadow: 5px 3px 8px 4px rgb(4, 163, 158);
}
.chartBoxLarge {
  margin: 0 25px 0 25px;
}
.chartContainer {
  width: 400px;
  height: 400px;
}
.chartContainer-mobile {
  width: 340px;
  height: 340px;
}
#userContainer {
  min-width: 80px;
  margin-top: 25px;
  padding-bottom: 15px;
  border-radius: 15px;
  /* background-color: var(--compo-bg-color); */
  background-color: var(--main-text-color-dark);
  color: var(--main-bg-color);
}
.userBlock {
  display: flex;
  justify-content: center;
  align-items: center;
  /* border-radius: 15px; */
  height: 50px;
  font-size: 0.9rem;
  /* margin: 20px 0; */
}
.userBlock:hover {
  cursor: pointer;
  color: var(--sub-text-color);
}

.profile-container {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}
.profile-container:hover {
  cursor: pointer;
  color: var(--sub-text-color);
}

.profile-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
}
.profile-image:hover {
  outline: 2px solid var(--sub-text-color);
  cursor: pointer;
}

.profile-name {
  margin: 10px 0 20px 0;
  font-size: 1rem;
}

.smallScreen {
  display: none;
}
.leftSide {
  display: flex;
  justify-content: center;
  color: var(--main-text-color);
  width: 340px;
  max-width: 340px;
  border-radius: 15px;
  border-color: var(--main-text-color);
  border-width: 1px;
  border-style: solid;
}
.leftSideCompo0 {
  height: 100px;
  min-height: 100px;
}
.leftSideCompo1 {
  height: 150px;
}
.leftSideCompo2 {
  height: 120px;
}
.leftSideCompo3 {
  /* margin-top: 20px; */
  height: auto;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;

  height: 200px;

  border: none;
}

.sideCompanyTitle {
  font-size: 3rem;

  color: var(--sub-text-color);
  align-items: center;

  border: none;
  height: 120px;
}
.sideCompanyTitle:hover {
  cursor: pointer;
}

.leftSideButton {
  margin: 5px 0;
  color: var(--main-bg-color);
  font-size: 4ex;
  border-radius: 12px;
  width: 100px;
  height: 85px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--main-text-color-dark);
}
.leftSideButton:hover {
  background-color: var(--sub-text-color);
  cursor: pointer;
}
.buttonCaption {
  display: block;
  margin: 5px 0px 0px 0px;
  font-size: 1.1rem;
}
.buttonCaption {
  color: var(--main-bg-color);
}

.clicked {
  background-color: var(--main-text-color);
}

.clicked2 {
  background-color: var(--sub-text-color);
}

/* 로그인 파트 */
#loginForm {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login {
  font-size: 1.5rem;
}
.loginMsg {
  margin: 50px 0 30px 0;
}
.loginBox {
  margin: 22px 0 0 0;
  font-size: 2rem;
  height: 45px;
}
.loginInput {
  width: 180px;
  height: 40px;
  border: none;
  border-radius: 5px;
  font-size: 1.3rem;
  text-align: center;
}
.loginInput:focus {
  outline: none;
}
.loginButton {
  margin: 50px 0;
  font-size: 2.2rem;
  color: var(--main-bg-color);
  width: 180px;
  height: 75px;
  background-color: var(--main-text-color-dark);
  border-radius: 10px;

  display: grid;
  place-items: center;
}
.loginButton:hover {
  cursor: pointer;
  background-color: var(--sub-text-color);
}
.hidden {
  display: none;
}

/*  */
.button {
  margin: 50px 0;
  font-size: 2.2rem;
  color: var(--main-bg-color);
  width: 250px;
  height: 100px;
  background-color: var(--main-text-color-dark);
  border-radius: 10px;

  display: grid;
  place-items: center;
}
.button:hover {
  cursor: pointer;
  background-color: var(--sub-text-color);
}

@media screen and (min-width: 850px) and (max-width: 1299px) {
  .section2,
  .verticalEmpty2,
  .main {
    width: 410px !important;
  }
}
@media screen and (min-width: 1300px) and (max-width: 1799px) {
  .section2,
  .verticalEmpty2,
  .main {
    width: 810px !important;
  }
}
@media screen and (min-width: 1800px) {
  .section2,
  .verticalEmpty2,
  .main {
    width: 1210px !important;
  }
}

/* 화면 크기가 최하로 줄어들 때 */
@media (max-width: 650px) {
  .App {
    min-width: 340px;
    max-width: 340px;
    display: flex;
    flex-direction: column; /* 세로 배치로 설정 */
    /* width: 100%; */
    box-sizing: border-box;
    justify-content: flex-start;
    align-items: center; /* 자식 요소들이 부모 컨테이너의 너비를 가득 채우도록 설정 */
    height: 100vh;
  }
  .section2,
  .verticalEmpty2,
  .main,
  .chartBox {
    width: 100%;
  }
  .section {
    display: flex;
    flex-direction: column;
    height: auto; /* 100vh;*/
    box-sizing: border-box;
    /* height: 33.33%; */
  }
  .section0 {
    max-height: 30px;
  }
  .section1 {
    max-width: 100%;
    align-items: center;
    min-height: 450px;
    /* height: 45%; */
  }
  .smallScreen {
    /* display: block; */
  }

  .section3 {
    display: none;
  }
  .leftSideCompo2 {
    display: none;
  }
  .leftSideCompo1 {
    display: none;
  }
  .empty {
    max-height: 30px;
  }
  .main {
    justify-content: center;
  }

  .verticalEmpty2 {
    /* display: none; */
  }
}
